
*{
  margin: 0px;
  padding:0px;
  box-sizing: border-box;

}
nav{
  display: flex;
  /* background: #1e1346; */
  align-items: center;
  /* color: #fff */
  
}
nav a{
  color: #fff;
  text-decoration: none;
}
nav>span{
  color:#fff;
  font-size: 35px;
  margin-left: 10px;
  cursor: pointer;
  position: relative;
  padding: 0px 20px;
  text-align: center;
}
.circle{
    width: 100%;
    height: calc(100% - 10px);
    border:2px solid #fff;
    position: absolute;
    left: 0px;
    top: 6px;
    /* margin: 5px; */
    border-radius: 50%;
   margin-right: 25px;
}
.logo{
  height: 50px;
  width: 50px;
  margin-left: 3px;
  animation-name: lo;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  cursor: pointer;
}
@keyframes lo{
    0%{
      transform: rotate(0deg);
    }
    100%{
      transform: rotate(360deg);
    }
}
nav .navbar{
  display: flex;
  list-style: none;
  padding: 10px 0px;
  position: relative;
}
nav .toogleb{
   position:absolute;
   right:15px;
  }
  nav .toogleb input[type="checkbox"]{
  cursor: pointer;
  width: 60px;
  background-color: #fff;
  /* border: 1px solid white; */
  height: 30px;
  -webkit-appearance: none;
  border-radius: 25px;
  /* padding:15px 20px; */
}
nav .toogleb input[type="checkbox"]::before{
content: "";
position: absolute;
top: 1px;
height: 28PX;
left: 0;
width:28PX;
border-radius: 50%;
background-color: grey;
transition: all .5s;
}
nav .toogleb input[type="checkbox"]:checked::before{
  left:calc(100% - 28px);
  top:0px;
  /* width: 100%; */
  border: 1px solid #fff;
  background-color: rgb(27, 27, 248);
}
nav .toogleb input[type="checkbox"]:checked{
  background-color: rgb(61, 61, 233);

}
nav .navbar li{
  color: #fff;
  margin: 5px 15px;
  padding-bottom: 3px;
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
nav .navbar li span{
  width: 100%;
  height: 2px;
  background: #fff;
  position: absolute;
  left: -100%;
  bottom: 0px;
  transition: left 0.3s;
  transition-timing-function:linear;
}

nav .navbar li:nth-child(1):hover>span,
nav .navbar li:nth-child(2):hover>span,
nav .navbar li:nth-child(3):hover>span,
nav .navbar li:nth-child(4):hover>span{
   /* color: blue; */
   background: #FFF;
   left: 0px;
}

